/* == Colors == */

/* Primary */
$light-red: hsl(356, 100%, 66%); //CTA text
$very-light-red-user: hsl(355, 100%, 74%); //CTA hover background
$very-dark-blue: hsl(208, 49%, 24%); //headings

/* Neutral */
$white: hsl(0, 0%, 100%); //text
$light-grayish-blue: hsl(240, 6%, 94%); //list background
$grayish-blue: hsl(240, 2%, 79%); //footer text
$very-dark-grayish-blue: hsl(207, 13%, 34%); //body copy
$very-dark-black-blue: hsl(240, 10%, 16%); //footer background

/* Gradient */
$very-light-red: hsl(13, 100%, 72%); 
$light-red: hsl(353, 100%, 62%);
$intro-gradient: linear-gradient(150deg, $very-light-red, $light-red); //intro/CTA mobile nav
$very-dark-gray-blue: hsl(237, 17%, 21%);
$very-dark-desaturated-lue: hsl(237, 23%, 32%);
$body-gradient: linear-gradient(150deg, $very-dark-gray-blue, $very-dark-desaturated-lue); //background gradient body

/* Fonts */
$fs: 1rem;

$ff-overpass: 'Overpass', sans-serif;
$fw-overpass-light: 300;
$fw-overpass-semibold: 600;

$ff-ubuntu: 'Ubuntu', sans-serif;
$fw-ubuntu-regular: 400;
$fw-ubuntu-medium: 500;
$fw-ubuntu-bold: 700;

/* Media query */
$min-width: 48rem;